import React from 'react'
import { css } from '@emotion/core'
import { Card, Layout, Button } from 'antd'
import IncomeReport from './incomeReport'

const Dashboard = () => {

  const tes = css`
    background: #fff;
    border-radius: 8px;
    border-top: 4px solid rgb(24, 144, 255);
    box-shadow: 1px 4px 15px rgba(24, 144, 255, 0.1);
  `

  const grid = css`
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-gap: 2em;
    @media(max-width: 768px){
      grid-template-columns: 1fr;
    }
  `

  const gridVehicle = css`
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 1em;
    @media(max-width: 768px){
      grid-template-columns: 1fr;
    }
  `

  const cardVehicle = css`
    display: grid;
    grid-template-columns: 1fr 2fr;
    align-items: center;
    h1 {
      font-size: 1.5em;
      color: #959eab;
    }
    h2 {
      font-size: 14px;
    }
    img {
      width: 40px;
      height: 40px;
    }
  `

  const cardMoney = css`
    display: grid;
    grid-template-columns: 2fr 3fr;
    align-items: center;
    padding: 1em;
    h1 {
      font-size: 2em;
      color: #959eab;
    }
    img {
      width: 100px;
      height: 100px;
    }
    > div {
      display: grid;
      button {
        justify-self: end; 
        transition: 0.3s !important;
        &:hover {
          box-shadow: 0px 5px 10px rgba(24, 144, 255, 0.2) !important;
          transform: translateY(-3px) !important;
        } 
      }
    }
  `

  const nominalIncome = (
    <div css={ grid }>
      <Card bordered={false} css={ tes }>
        <div css={cardMoney}>
          <img src="/static/icons/cash.png" />
          <div>
            <h2>Cash</h2>
            <h1>Rp. 1.235.000.000</h1>
            <Button type="primary" ghost>Detail</Button>
          </div>
        </div>
      </Card>
      <Card bordered={false} css={ tes }>
        <div css={cardMoney}>
          <img src="/static/icons/noncash.png" />
          <div>
            <h2>Non Cash</h2>
            <h1>Rp. 1.235.000.000</h1>
            <Button type="primary" ghost>Detail</Button>
          </div>
        </div>
      </Card>
    </div>
  )

  const numberOfVehicle = (
    <div css={ gridVehicle }>
      <Card bordered={false} css={ tes }>
        <div css={cardVehicle}>
          <img src="/static/icons/motor1.png" />
          <div>
            <h2>Motorcycles</h2>
            <h1>0/100</h1>
          </div>
        </div>
      </Card>
      <Card bordered={false} css={ tes }>
        <div css={cardVehicle}>
          <img src="/static/icons/car.png" />
          <div>
            <h2>Car</h2>
            <h1>0/100</h1>
          </div>
        </div>
      </Card>
      <Card bordered={false} css={ tes }>
        <div css={cardVehicle}>
          <img src="/static/icons/motor.png" />
          <div>
            <h2>Motorcycles &gt; 250cc</h2>
            <h1>0/100</h1>
          </div>
        </div>
      </Card>
      <Card bordered={false} css={ tes }>
        <div css={cardVehicle}>
          <img src="/static/icons/bigcar.png" />
          <div>
            <h2>Big Car</h2>
            <h1>0/100</h1>
          </div>
        </div>
      </Card>
    </div>
  )

  return (
    <Layout css={css`
      display: grid;
      grid-row-gap: 1em;
    `}>
      <Card title="NOMINAL INCOME" bordered={false} type="inner">
        { nominalIncome }
      </Card>
      <Card title="NUMBER OF VEHICLES" bordered={false} type="inner">
        { numberOfVehicle }
      </Card>
      <Card title="INCOME REPORT" bordered={false} type="inner">
        <div>
          <IncomeReport />
        </div>
      </Card>
    </Layout>
  )
}

export default Dashboard